import React, { useEffect, useState } from 'react';
import VideoPlayer from './components/VideoPlayer';

// 
const sampleVideos = [
  {
    id: 1,
    title: ' 1',
    description: 'Livepeer ',
    playbackId: 'YOUR_PLAYBACK_ID_1', //  playbackId
  },
  {
    id: 2,
    title: ' 2',
    description: '',
    playbackId: 'YOUR_PLAYBACK_ID_2', //  playbackId
  },
];

function App() {
  const [selectedVideo, setSelectedVideo] = useState(null);
  const [tgUser, setTgUser] = useState(null);

  useEffect(() => {
    //  Telegram Web App
    if (window.Telegram?.WebApp) {
      const tg = window.Telegram.WebApp;
      
      // 
      tg.expand();
      
      // 
      tg.enableClosingConfirmation();
      
      // 
      if (tg.initDataUnsafe?.user) {
        setTgUser(tg.initDataUnsafe.user);
      }
      
      // 
      tg.MainButton.setText('');
      tg.MainButton.onClick(() => {
        if (selectedVideo) {
          tg.showAlert(`: ${selectedVideo.title}`);
        }
      });
      
      // /
      if (selectedVideo) {
        tg.MainButton.show();
      } else {
        tg.MainButton.hide();
      }
    }
    
    // 
    if (!selectedVideo && sampleVideos.length > 0) {
      setSelectedVideo(sampleVideos[0]);
    }
  }, [selectedVideo]);

  const handleVideoSelect = (video) => {
    setSelectedVideo(video);
    
    // Telegram Mini App 
    if (window.Telegram?.WebApp?.HapticFeedback) {
      window.Telegram.WebApp.HapticFeedback.impactOccurred('light');
    }
  };

  return (
    <div className="container">
      <div className="header">
          <h1> Livepeer </h1>
          {tgUser && (
            <p style={{ marginTop: '8px', fontSize: '14px', opacity: 0.7 }}>
              , {tgUser.first_name}!
            </p>
          )}
        </div>

        {selectedVideo ? (
          <div className="player-container">
            <VideoPlayer playbackId={selectedVideo.playbackId} />
          </div>
        ) : (
          <div className="loading"></div>
        )}

        <div className="video-list">
          <h2 style={{ marginBottom: '16px', fontSize: '18px' }}></h2>
          {sampleVideos.map((video) => (
            <div
              key={video.id}
              className={`video-item ${selectedVideo?.id === video.id ? 'active' : ''}`}
              onClick={() => handleVideoSelect(video)}
            >
              <h3>{video.title}</h3>
              <p>{video.description}</p>
            </div>
          ))}
        </div>
      </div>
  );
}

export default App;
